// Atividades.tsx
import React, { useState } from "react";
import SlideImage from '../../components/SlideImages/SlideImages'
import './Atividades.css'
import { CarouselProvider } from "pure-react-carousel";


const Atividades = () => {

    const images = [
        { url: require('../../assets/limpeza-1.jpg') },
        { url: require('../../assets/limpeza-2.jpg') },
        { url: require('../../assets/limpeza-3.jpg') },
        { url: require('../../assets/limpeza-4.jpg') },
        { url: require('../../assets/limpeza-5.jpg') }
    ];

    const images2 = [
        { url: require('../../assets/gatos-1.jpg') },
        { url: require('../../assets/gatos-2.jpg') },
    ];

    const descricaoLimpeza = `
    A equipa do "Todos Somos Especiais" participou na VII edição da Ação de Limpeza de Esposende.\n 
    Durante a atividade, lamentamos a grande quantidade de lixo encontrada na praia, mas ficamos felizes por existirem iniciativas como esta.\n 
    Relembramos a importância de preservar o meio ambiente, evitando deitar lixo no chão.\n 
    Convidamos todos a observar a quantidade de lixo nas bermas das estradas e outros locais que frequentemente passam despercebidos, e a participar em ações de limpeza como esta. Junte-se a nós para fazer a diferença! 💚`
    const descricaoGatil =  `
    No "Todos Somos Especiais", tivemos o prazer de visitar um gatil e ajudar animais de rua. Foi um dia maravilhoso, repleto de alegria, amor e dedicação. Agradecemos imensamente pela acolhida calorosa e pelos momentos de carinho compartilhados com os gatinhos.\n
    A dona do gatil expressou sua gratidão:\n
    "Foi um prazer enorme visitar-vos e aos gatinhos! Muito obrigada a todos pela visita, alegria e amor aos animais de rua. Obrigada pelos miminhos para todos. Bem hajam! Beijinho e felicidades para todos."\n
    Nosso domingo foi incrível, marcado pela nossa primeira ação de amor e generosidade com os animais. Nós, um grupo de jovens de Barcelos, criamos a associação "Todos Somos Especiais" para ajudar animais e pessoas. Amor aos gatos, alegria e boa disposição não faltaram. Adoramos a visita e somos gratos pelos mimos aos gatos e cães. Voltem sempre. Beijinho a todos e continuem assim pela vida fora, respeitando e amando os animais de rua.\n
    "As pessoas que são loucas o suficiente para acharem que podem mudar o mundo são as que, de facto, o mudam." – Steve Jobs\n
    Obrigada @caudas_e_bigodes ❤️\n
    Participe conosco e ajude a fazer a diferença na vida dos animais!
    `;

    return (
        <>
            <div className='Atividades'>
                <div className='header'>
                    <h1>Atividades</h1>
                </div>
                <CarouselProvider
                    visibleSlides={1}
                    totalSlides={5}
                    naturalSlideWidth={500}
                    naturalSlideHeight={500}
                    interval={3000}
                    infinite={true}
                >

                    <SlideImage images={images} titulo={"Juntos Pela Limpeza de Esposende"} descricao={descricaoLimpeza} />
                </CarouselProvider>
                <CarouselProvider
                    visibleSlides={1}
                    totalSlides={2}
                    naturalSlideWidth={500}
                    naturalSlideHeight={500}
                    interval={3000}
                    infinite={true}>
                    <SlideImage images={images2} titulo={"Visita ao Gatil: Amor e Cuidado com os Animais"} descricao={descricaoGatil} />
                </CarouselProvider>
            </div>
        </>
    )
}

export default Atividades;
